export default {
	data() {
		return {
			numPerPage:20,
			Data:[],
			add:''
		}
	},
	created() {
		this.init()
		uni.showLoading({
		    title: '加载中'
		});
	},
	onReachBottom: function (){
		console.log('触底了')
		this.numPerPage+=20
		console.log(this.numPerPage)
		this.init()
	},
	methods: {
		init(){
			uni.request({
							url: this.$store.state.url + '/System/GetBacklog',
							method: 'GET',
							data: {
								pageNum: '1',
								numPerPage: this.numPerPage,
								orderField: '',
								orderDirection: '',
								search :this.add,
							},
							header: {
								'Token': this.$store.state.token
							},
							success: (res) => {
								var asd = JSON.parse(res.data).Data
								this.Data = asd.BacklogDtos
								console.log(this.Data)
								for(var i = 0; i < this.Data.length; i ++){
									if(this.Data[i].Describe.length>25){
										this.Data[i].Describe = this.Data[i].Describe.slice(0,25)+'....'
									}
								}
								uni.showLoading({
								    title: '加载中'
								});
								setTimeout(function () {
								    uni.hideLoading();
								}, 100);
							}
					});

		},
	}
}
